import React, { useState, useEffect, useContext, useRef } from 'react'
import { Spin,Typography, Table, Input, Tag, Button, Modal, message, Select } from 'antd'

import { Link } from 'react-router-dom'
import FileContext from '../../../../../context/file/fileContext'
import Spinner from '../../../../laout/Spinner'
import qs from 'qs'
import axios from 'axios'
import '../File2/File2.scss'
import moment from 'moment'



const File2 = (props) => {

    //
    const { Text } = Typography
    const fileUpload = useRef(null) //
    const [path, setPath] = useState('')


    //context
    const fileContext = useContext(FileContext)
    const { getFile, file = [], count = 0, loading } = fileContext
    const fileArr = ['','','','']

    //
    useEffect(() => {  //useEffect didMount
        getFile(props.match.params.param, 5, 1) //
        // eslint-disable-next-line
    }, [props.match.params.param])

    //    
    const [visible, setVisible] = useState(false)
    const [visible1, setVisible1] = useState(false)

    //
    const [delId, setDelId] = useState('')//id


    //
    const showModal = (id) => {
        setVisible(true)
        console.log('delId: ' + id)
        setDelId(id)
    };

    const handleOk = e => {
        setVisible(false)
        axios.post('/zjj/myFile/del', { _id: delId }).then(
            (data) => {
                if (data.status === 200) {
                    if (data.data.err === -1) {
                        message.info('!');
                        return
                    }
                    else if (data.data.err === -2) {
                        message.info('');
                        return
                    }
                    else {
                        message.info('~');
                        setTimeout(() => {
                            getFile(props.match.params.param, 5, 1) //
                        }, 200)
                    }
                }
            })
    };

    const handleCancel = e => {
        setVisible(false)
    };
    //
    const clearPath = () => {
        setPath('')
    }

    //

    const [name, setName] = useState(''); // reactHook  useStatestate

    const handleNameChange = (val) => {
        if (val.target.value.length > 30) { return }
        setName(val.target.value)
    }

    const showModal1 = () => {
        setVisible1(true)
    };

    const clearData = () => {
        setName('')
        setPath('')
    }


    // -- 
    //
    const handleUpload = () => {

        let config = {
            headers: {
                'Content-Type': 'multipart/form-data'
            }
        }
        //
        if (fileUpload.current.files[0] === undefined) {
            message.info('!')
            return
        }

        //formData
        let formData = new FormData()
        formData.append('file', fileUpload.current.files[0])

        axios.post('/zjj/file/uploadFile', formData, config).then(data => {
            if (data.status === 200) {
                if (data.data.err === -1) {
                    message.info('')
                    clearPath()
                    return
                }
                if (data.data.err === -2) {
                    message.info('')
                    clearPath()
                    return
                }
                // let link = 'http://l1nkkk.xyz:3001'
                // let link = '/zjj'
                let imgUrl = data.data.img

                console.log(imgUrl)
                // return updateImg(imgUrl)
                setPath(imgUrl)
                message.info('')

            }
        }).catch(err => {
            message.info(err)
            clearPath()
        })


    }


    //
    const handleOk1 = e => {
        setVisible1(false)
        //
        if (fileUpload.current.files[0] === undefined || !path) {
            message.info('!')
            clearData()
            return
        }

        if (!name) {
            message.info('')
            clearData()
            return
        }
        if (name.length < 1 || name.length > 30) {
            message.info('')
            clearData()
            return
        }
        let time = String(Date.parse(new Date()))
        let username = localStorage.getItem('name')

        console.log(username,time,'name&time')
        //formData
        //
        axios.post('/zjj/myFile/addFile',
            qs.stringify({
                type: props.match.params.param,
                name,
                path,
                author:username,
                time,
            }),
            {
                headers: {
                    'content-type': 'application/x-www-form-urlencoded;charset=UTF-8',
                    'Accept': 'application/json;charset=UTF-8'
                }
            }
        ).then(
            (data) => {
                console.log(data)
                if (data.status !== 200) {
                    message.info('')
                    clearData()
                    return
                }
                if (data.data.err === -2) {
                    message.info(data.data.msg)
                    clearData()
                    return
                }
                if (data.data.err === -1) {
                    message.info(data.data.msg)
                    clearData()
                    return
                }
                //
                clearData();
                setCurrentPage(1)
                message.info('');
                setTimeout(() => {
                    getFile(props.match.params.param, 5, 1) //
                }, 200)
            }, () => {
                return Promise.reject('')
            }).catch(err => {
                message.info(err)
                console.log(err)
                clearData()
            })
    };

    const handleCancel1 = e => {
        setVisible1(false)
        clearData()

    };

    //
    const [currentPage, setCurrentPage] = useState(1)
    const paginationProps = {
        showSizeChanger: false,
        showQuickJumper: false,
        showTotal: () => `${count}`,
        pageSize: 5,
        current: currentPage,
        total: count,
        onShowSizeChange: (current, pageSize) => changePageSize(pageSize, current),
        onChange: (current) => changePage(current),
    }

    const changePageSize = (a, b) => {
        console.log(a, b)
    }

    const changePage = (v) => {
        console.log(v)
        setCurrentPage(v)
        getFile(props.match.params.param, 5, v) //


    }

    // const handleDownload = (path,name) =>{
    //     console.log(path)
    //     //abody
    //     const oa = document.createElement('a')
    //     oa.href =  'http://52.80.161.97:9616/AutomaticOfficeSystem/files_cache/ca9aee4fd2964d19bbfdd674a4377ab6.pdf'
    //     oa.target="_blank"; //  ie 
    //     oa.setAttribute('target','_blank')
    //     oa.setAttribute('download',path)
    //     // oa.download =name
    //     document.body.appendChild(oa)
    //     oa.click()
    //     document.body.removeChild(oa)


    // }





    const columns = [
        {
            title: '',
            dataIndex: 'title',
            key: 'title',
        },
        {
            title: '',
            dataIndex: 'date',
            key: 'date',
        },
        {
            title: '',
            dataIndex: 'username',
            key: 'username',
        },


        {
            title: '',
            key: 'action',
            render: (text, record, index) => (
                <span>
                    <span><a download href={record.link} style={{ color: 'red' }}></a></span>
                </span>
            ),
        },
    ];

    const data = [];
    if (file.length > 0) {
        file.forEach((item, index) => {
            data.push({
                key: item._id,
                title: item.name ? item.name : '',
                date: item.time ? moment(Number(item.time)).format('YYYY-MM-DD') : '',
                username: item.author ? item.author : '',
                link: item.path ? item.path : ''
            })
        })
    }



    if (loading) { return <Spin className="myLoading" tip="..."  size='large'>
         <div className='File2'>
            <div>
                <Modal
                    title=""
                    visible={visible1}
                    onOk={handleOk1}
                    onCancel={handleCancel1}
                    destroyOnClose={true}
                >
                    <div className='File2-wrap__div'>
                        <Text style={{ fontSize: 'normal' }} className="File2-wrap__text"></Text>
                        <Input
                            className="File2-wrap__input"
                            placeholder=""
                            value={name}
                            onChange={handleNameChange}
                        />
                    </div>
                    <div className='File2-wrap__div' style={{ position: 'relative' }}>
                        <Text style={{ textAlign: 'center', fontSize: 'normal' }} className="File2-wrap__text"></Text>
                        <input type="file" className='File2-wrap__input' ref={fileUpload} />
                        <Button className='File2-btn' type='danger' onClick={handleUpload}></Button>

                    </div>

                </Modal>
            </div>
            <div>
                <Modal
                    title=''
                    visible={visible}
                    onOk={handleOk}
                    onCancel={handleCancel}
                >
                    <p style={{ fontWeight: 'bold' }}>
                        
                    </p>
                </Modal>
            </div>
            <div className='navText'>
                <Text strong style={{ color: '#ff0000' }} className='navFont'>{fileArr[props.match.params.param-1]}</Text>
            </div>
            <div className='File2-wrap'>
                <Table columns={columns} dataSource={data} pagination={paginationProps} />
            </div>

        </div>
    </Spin>}
    return (
        <div className='File2'>
            <div>
                <Modal
                    title=""
                    visible={visible1}
                    onOk={handleOk1}
                    onCancel={handleCancel1}
                    destroyOnClose={true}
                >
                    <div className='File2-wrap__div'>
                        <Text style={{ fontSize: 'normal' }} className="File2-wrap__text"></Text>
                        <Input
                            className="File2-wrap__input"
                            placeholder=""
                            value={name}
                            onChange={handleNameChange}
                        />
                    </div>
                    <div className='File2-wrap__div' style={{ position: 'relative' }}>
                        <Text style={{ textAlign: 'center', fontSize: 'normal' }} className="File2-wrap__text"></Text>
                        <input type="file" className='File2-wrap__input' ref={fileUpload} />
                        <Button className='File2-btn' type='danger' onClick={handleUpload}></Button>

                    </div>

                </Modal>
            </div>
            <div>
                <Modal
                    title=''
                    visible={visible}
                    onOk={handleOk}
                    onCancel={handleCancel}
                >
                    <p style={{ fontWeight: 'bold' }}>
                        
                    </p>
                </Modal>
            </div>
            <div className='navText'>
                <Text strong style={{ color: '#ff0000' }} className='navFont'>{fileArr[props.match.params.param-1]}</Text>
            </div>
            <div className='File2-wrap'>
                <Table columns={columns} dataSource={data} pagination={paginationProps} />
            </div>

        </div>
    )
}

export default React.memo(File2)
