import React, { useState, useContext, useEffect } from 'react'
import { Typography, Input, Table, Tag, message } from 'antd'
import CronContext from '../../../../../context/crontab/cronContext'
import axios from 'axios'
import moment from 'moment'

import '../Log/Log.scss'



const Log = (props) => {
    const [total,setTotal]=useState(0)
    const [logData, setLogData] = useState({})
    const cronContext = useContext(CronContext)
    const { node, getNode, } = cronContext



    const { logName } = props //


    useEffect(() => {  //useEffect didMount
        getLog()

        let task = node[logName].name

        console.log('log_name', task)
        // eslint-disable-next-line
    }, [])


    //
    const getLog = () => {
        //axiospostJSON.Stringify
        //URLSearchParams
        let param = new URLSearchParams()
        let task = node[logName].name
        param.append('name', task)

        axios.get('/crontab/job/log', param)
            .then((data) => {
                if (data.status === 200) {
                    if (data.data.errno !== 0) {
                        message.info('');
                        return
                    }
                    if(data.data.data.length===0){
                        setLogData({})
                    }
                    if (data.data.data.length > 0) {
                        setLogData(data.data.data)
                        setTotal(data.data.data.length)
                    }
                    console.log(data.data.data)

                }
            },()=>{
                message.info('')
            })
            .catch((err) => { message.info('') })
    }


    //
    const [currentPage, setCurrentPage] = useState(1)
    const paginationProps = {
        showSizeChanger: false,
        showQuickJumper: false,
        showTotal: () => `${total}`,
        pageSize: 5,
        current: currentPage,
        total: total,
        onShowSizeChange: (current, pageSize) => changePageSize(pageSize, current),
        onChange: (current) => changePage(current),
    }

    const changePageSize = (a, b) => {
        console.log(a, b)
    }

    const changePage = (v) => {
        console.log(v)
        setCurrentPage(v)
    }

    // const paginationProps = {
    //     showSizeChanger: true,
    //     showQuickJumper: false,
    //     showTotal: () => `${totals}`,
    //     pageSize: this.state.pageSize,
    //     current: page.pageNum,
    //     total: page.total,
    //     onShowSizeChange: (current, pageSize) => this.changePageSize(pageSize, current),
    //     onChange: (current) => this.changePage(current),
    // }






    const columns = [
        {
            title: '',
            dataIndex: 'name',
            key: 'name',
            render: text => <span>{text}</span>,
        },
        {
            title: 'shell',
            dataIndex: 'shell',
            key: 'shell',
            render: text => <span>{text}</span>,
        },
        {
            title: '',
            dataIndex: 'false',
            key: 'false',
        },
        {
            title: '',
            dataIndex: 'output',
            key: 'output',
        },
        {
            title: '',
            dataIndex: 'planTime',
            key: 'planTime',
        },
        {
            title: '',
            dataIndex: 'realTime',
            key: 'realTime',
        },
        {
            title: '',
            dataIndex: 'begin',
            key: 'begin',
        },
        {
            title: '',
            dataIndex: 'end',
            key: 'end',
        },

    ];

    const data = []; // antd
    if (logData.length !== undefined) {
        logData.forEach((item, index) => {
            data.push({
                key: String(index + 1),

                name: item.jobName ? item.jobName : '',
                shell: item.command ? item.command : '',
                false: item.err ? item.err : '',
                output: item.output ? item.output : '',
                planTime: item.planTime ? moment(item.planTime).format('YYYY-MM-DD HH:mm:ss.SSS') : '',
                realTime: item.scheduleTime ? moment(item.scheduleTime).format('YYYY-MM-DD HH:mm:ss.SSS') : '',
                begin: item.startTime ? moment(item.startTime).format('YYYY-MM-DD HH:mm:ss.SSS') : '',
                end: item.endTime ? moment(item.endTime).format('YYYY-MM-DD HH:mm:ss.SSS') : '',


            })

        })
    }

    // if (loading) { return (<Spinner style={{ width: '300px' }}></Spinner>) }
    return (
        <div className='Log'>
            <div className='Log-table' style={{ marginBottom: '10px' }}>
                <Table columns={columns} dataSource={data} pagination={paginationProps} />
            </div>
        </div>
    )
}

export default Log




