import React, { useReducer } from 'react';
import MonitorContext from './monitorContext';
import MonitorReducer from './monitorReducer';
import axios from 'axios'
import { message } from 'antd'

import { GET_DONE_LIST,GET_MONITOR, SET_LOADING, GET_DETAIL, CLEAR_ID } from '../type'



const MonitorState = props => {
    //
    const initialState = {
        monitor: [],
        detail: [],
        loading: false,
        total: 0,
        currentId: 0,

        doneList:[], //
        doneListNum:0,

    };

    //useReducerstateReducer
    const [state, dispatch] = useReducer(MonitorReducer, initialState) //1reduver       2 

    //                                                          
    //getMonitor
    const getMonitor = async () => { //loginuser
        setLoading(true)//loading
        let res = null
        try {
            res = await axios.get('/api/hosts/status', {})
        } catch (err) {
            message.info('')
            // return Promise.reject()
        }

        if (res.status !== 200) {
            message.info('')
            setLoading(false)
            return
        }
        dispatch({
            type: GET_MONITOR,
            payload: {
                monitor: res.data,
                total: res.data.length
            }
        })
        setLoading(false)
    };
    // api/graphs/?host_id=3&time_range=latest  


    //getMonitorDetail
    const getMonitorDetail = async (id) => { //loginuser
        setLoading(true)//loading
        let res = null
        try {
            res = await axios.get(`/api/graphs/?host_id=${id}&time_range=latest`)
        }
        catch (err) {
            message.info(err)
            return Promise.reject()
        }
        if (res.status !== 200) {
            message.info('')
            setLoading(false)
            return
        }
        dispatch({
            type: GET_DETAIL,
            payload: {
                detail: res.data[0].data,
                currentId: id
            }
        })
        setLoading(false)
    };

    //
    const getDoneList = async (id) => { //loginuser
        setLoading(true)//loading
        let res = null
        try {
            res = await axios.get(`/api/triggers_list/?host_id=${id}`)
        }
        catch (err) {
            message.info(err)
            return Promise.reject()
        }
        if (res.status !== 200) {
            message.info('')
            setLoading(false)
            return
        }
        dispatch({
            type: GET_DONE_LIST,
            payload: {
                doneList: res.data[0].event_list,
                doneListNum:res.data[0].event_list.length
            }
        })
        setLoading(false)
    };

    //set loading
    const setLoading = (loading) => {
        dispatch({
            type: SET_LOADING, //dispatchReducer
            payload: { loading }
        })
    }

    //id
    const clearId = () => {
        dispatch({
            type: CLEAR_ID,
        })

    }

    return (<MonitorContext.Provider value={{
        monitor: state.monitor,
        loading: state.loading,
        total: state.total,
        detail: state.detail,
        currentId: state.currentId,
        doneList:state.doneList, //
        doneListNum:state.doneListNum,

        setLoading,
        getMonitor,
        getMonitorDetail,
        clearId,
        getDoneList,
    }}>{props.children}</MonitorContext.Provider>)


}
export default MonitorState
