import React, { useReducer } from 'react';
import UserContext from './userContext';
import UserReducer from './userReducer';
import axios from 'axios'
import {message} from 'antd'

import { GET_INITIAL_USERS,SET_MODE,GET_USERS,GET_SEARCH_USER,GET_LOGINUSER,GET_ALLUSER,ClEAR_USER, SET_LOADING } from '../type'



const UserState = props => {
    //
    const initialState = {
        list: {},
        allUser:[],//
        total:0,//
        loading: false,
        searchUser:[], //
        searchUserNum:0,//

        mode:false ,//mode--false  mode--true

        val:'',  //
        users: [], //  -- 
        usersNum:0,
        initialUser: [], // -- 
        
    };

    //useReducerstateReducer
    const [state, dispatch] = useReducer(UserReducer, initialState) //1reduver       2 

    //                                                          
    //getLoginUser
    const getLoginUser = async _id => { //loginuser
        setLoading(true)//loading   
        const res = await axios.post('/zjj/user/getUserById',{Id:_id})
        if(res.data.err===-1){
            message.info('')
            setLoading(false)
            return
        }
        if(res.data.err===-2){
            message.info('')
            setLoading(false)
            return
        }
        if(res.data.err=== -999){
            message.info('')
            setLoading(false)
            return
        }
        dispatch({
            type: GET_LOGINUSER,
            payload: {
                list: res.data.list[0]
            }
        })
        setLoading(false)
    };

     //
     const getAllUser = async (right,limit,page) => { //loginuser
        setLoading(true)//loading
        const res = await axios.post('/zjj/user/getUserByRight',{right,limit,page})
        if(res.data.err===-1){
            message.info('')
            setLoading(false)
            return
        }
        if(res.data.err===-2){
            message.info('')
            setLoading(false)
            return
        }
        if(res.data.err=== -999){
            message.info('')
            setLoading(false)
            return
        }
        dispatch({
            type: GET_ALLUSER,
            payload: {
                allUser: res.data.info.list,
                total: res.data.info.count,
            }
        })
        setLoading(false)
        setMode(false)

    };

     //
     const getSearchUser = async (Kw,limit,page) => { //loginuser
        setLoading(true)//loading
        setMode(true)//

        const res = await axios.post('/zjj/user/getUserByKw',{Kw,limit,page})
        if(res.data.err===-1){
            message.info('')
            setLoading(false)
            return
        }
        if(res.data.err===-2){
            message.info('')
            setLoading(false)
            return
        }
        if(res.data.err=== -999){
            message.info('')
            setLoading(false)
            return
        }
        dispatch({
            type: GET_SEARCH_USER,
            payload: {
                searchUser: res.data.info.list,
                searchUserNum: res.data.info.count,
            }
        })
        setLoading(false)
    };


     //
     const getUsers = async (limit,page) => { //loginuser
        setLoading(true)//loading
        const res = await axios.post('/zjj/user/getAllUser',{limit,page})
      
        if(res.data.err===-2){
            message.info('')
            setLoading(false)
            return
        }
        if(res.data.err=== -999){
            message.info('')
            setLoading(false)
            return
        }
        dispatch({
            type: GET_USERS,
            payload: {
                users: res.data.info.list,
                usersNum: res.data.info.count,
            }
        })
        setLoading(false)
        setMode(false)

    };


    //
    const getInitialUser = async () =>{
        let res = null
        try{
           res = await axios.get('/zjj/user/getInitialUser',{})
        }
        catch(err){
            message.info(err)
            return Promise.reject()
        }
        if(res.status!==200){
            message.info('')
            setLoading(false)
            return
        }
        if(res.data.err===-2){
            message.info('')
            setLoading(false)
            return
        }
        if(res.data.err=== -999){
            message.info('')
            setLoading(false)
            return
        }
        dispatch({
            type: GET_INITIAL_USERS,
            payload: {
                initialUser: res.data.info.list,
            }
        })
    }
 
    //clear users
    const clearUser = () => {
        dispatch({
            type: ClEAR_USER
        })
    };

    //set loading
    const setLoading = (loading) => {
        dispatch({
            type: SET_LOADING, //dispatchReducer
            payload: { loading }
        })
    }

     //set mode : false ===  ;   true === 
     const setMode = (mode) => {
        dispatch({
            type: SET_MODE, 
            payload: { mode }
        })
    }

    return (<UserContext.Provider value={{
        list: state.list,                 //
        allUser:state.allUser,            //
        total:state.total,                //
        searchUser:state.searchUser,      //
        searchUserNum:state.searchUserNum,//
        users:state.users,                //
        usersNum:state.usersNum,          //
        loading: state.loading,
        mode: state.mode,
        initialUser: state.initialUser, //

        setLoading,
        clearUser,
        getLoginUser,
        getAllUser,
        getSearchUser,
        getUsers,
        setMode,
        getInitialUser,
    }}>{props.children}</UserContext.Provider>)


}
export default UserState
