import React, { useState } from 'react'
import { NavLink } from 'react-router-dom'  //a
import { Typography, Input, Button, message } from 'antd'

import '../pwdManage/pwdManage.scss'
import axios from 'axios'




const PwdManage = (props) => {



    const [pwdOld, setPwdOld] = useState(''); // reactHook  useStatestate
    const [pwdNew1, setPwdNew1] = useState(''); // reactHook  useStatestate
    const [pwdNew2, setPwdNew2] = useState(''); // reactHook  useStatestate

    // const { MonthPicker, RangePicker } = DatePicker;

    // 
    const handlePwdOldChange = (val) => {
        if (val.target.value.length > 25) { return }
        setPwdOld(val.target.value)
    }
    const handlePwdNew1Change = (val) => {
        if (val.target.value.length > 25) { return }
        setPwdNew1(val.target.value)
    }
    const handlePwdNew2Change = (val) => {
        if (val.target.value.length > 25) { return }
        setPwdNew2(val.target.value)
    }

    const clearData = () => {
        setPwdNew1('')
        setPwdNew2('')
        setPwdOld('')
    }

    const handleSubmit = () => {
        let _id = localStorage.getItem('_id')
        if (!pwdOld || !pwdNew1 || !pwdNew2) {
            message.info('/')
            return
        }
        if (pwdNew2 !== pwdNew1) {
            message.info('')
            return
        }
        if (pwdNew1.length < 6) {
            message.info('6-25')
            return
        }
        if (pwdNew2.length < 6) {
            message.info('6-25')
            return
        }

        axios.post('/zjj/user/updatePwd', { _id, ps: pwdOld, newPs1: pwdNew1, newPs2: pwdNew2 })
            .then((data) => {
                if (data.status === 200) {
                    if (data.data.err === -3) {
                        message.info('');
                        clearData();
                    }
                    else if (data.data.err === -2) {
                        message.info(data.data.msg);
                        clearData();
                    }
                    else {
                        clearData();
                        return axios.post('/zjj/user/logOut', {}) //
                    }
                }

            }).then((data) => {
                if (data.status === 200) {
                    if (data.data.err === 0) {
                        //
                        message.info('');
                        localStorage.removeItem('name') //
                        localStorage.removeItem('login')
                        localStorage.removeItem('mode')

                        setTimeout(() => {
                            window.location.href = '/'
                        }, 1000)

                    } else {
                        message.info('');
                        return false
                    }
                }
            }, (err) => { message.info(err) })
            .catch((err) => { message.info(err) })

    }

    const { Text } = Typography

    return (
        <div className='pwdManage'>
            <div className='navText'>
                <Text strong style={{ color: '#ff0000' }} className='navFont'></Text>
            </div>
            <ul className='pwdManage-ul'>
                <li className='pwdManage-ul__li'><NavLink exact activeClassName="pwdManage-ul__li--active" className='Account-ul__li--link' to='/people'></NavLink></li>
                <li className='pwdManage-ul__li'><NavLink activeClassName="pwdManage-ul__li--active" className='Account-ul__li--link' to='/people/pwdManage'></NavLink></li>

            </ul>
            <div className='pwdManage-wrap'>
                <div className='pwdManage-wrap__box' style={{marginBottom:'20px'}}>
                    <div className='pwdManage-wrap__div' style={{ width: '65%' }}>
                        <Text className="pwdManage-wrap__text"></Text>
                        <Input

                            className="pwdManage-wrap__input"
                            placeholder=""
                            value={pwdOld}
                            onChange={handlePwdOldChange}
                        />
                    </div>
                </div>
                <div className='pwdManage-wrap__box'>

                    <div className='pwdManage-wrap__div' style={{ width: '65%' }}>
                        <Text className="pwdManage-wrap__text"></Text>
                        <Input
                            className="pwdManage-wrap__input"
                            placeholder=""
                            value={pwdNew1}
                            type='password'
                            onChange={handlePwdNew1Change}
                        />
                    </div>
                </div>

                <div className='pwdManage-wrap__box' style={{ marginTop: '25px' }}>
                    <div className='pwdManage-wrap__div' style={{ width: '65%' }}>
                        <Text className="pwdManage-wrap__text"></Text>
                        <Input
                            className="pwdManage-wrap__input"
                            placeholder=""
                            value={pwdNew2}
                            type='password'
                            onChange={handlePwdNew2Change}
                        />
                    </div>

                </div>




                <div className='pwdManage-wrap__bottom'>
                    <Button type='danger' className='pwdManage-wrap__bottom--btn' onClick={handleSubmit}></Button>
                </div>
            </div>

        </div>
    )
}

export default PwdManage
