import React, { useEffect, useState, useContext } from 'react'
import { Spin, Typography, Table, Input, Tag, Icon, Modal, Select, Button, message } from 'antd'
// import RightContext from '../../../../../context/right/rightContext'
import Spinner from '../../../../laout/Spinner'
import qs from 'qs'
import axios from 'axios'
import UserContext from '../../../../../context/user/userContext'

import '../Right2/Right2.scss'




const Right2 = (e) => {

    //
    const { Text } = Typography
    const { Search } = Input
    const { Option } = Select
    const addType = '' //

    //context

    const userContext = useContext(UserContext)
    const { getInitialUser, initialUser = [], getAllUser, allUser = [], loading, total = 0, allpage = 0 } = userContext

    //
    useEffect(() => {  //useEffect didMountwillUnMount

        getAllUser(2, 5, 1)
        getInitialUser()


        // eslint-disable-next-line
    }, [])



    // -- 
    const [username, setName] = useState(''); // reactHook  useStatestate

    const handleNameChange = (val) => {
        if (val.target.value.length > 30) { return }
        setName(val.target.value)
    }


    //    
    const [visible, setVisible] = useState(false)
    const [visible2, setVisible2] = useState(false)

    //
    const [delName, setDelName] = useState('')
    const [delUserId, setDelUserId] = useState('')


    //
    const showModal = () => {
        setVisible(true)
    };

    const clearName = () => {
        setName('')
    }

    const handleOk = e => {
        setVisible(false)
        let flag = false
        if (!username) {
            message.info('!')
            clearName()
            return
        }

        initialUser.forEach(e => {
            if (e.us === username) {
                flag = true
                console.log(e.us, username, '+++', e._id)
                // -- 2:
                axios.post('/zjj/user/updateRight2',
                    qs.stringify({
                        _id: e._id
                    }),
                    {
                        headers: {
                            'content-type': 'application/x-www-form-urlencoded;charset=UTF-8',
                            'Accept': 'application/json;charset=UTF-8'
                        }
                    }
                ).then(
                    (data) => {
                        console.log(data)
                        if (data.status === 200 || data.status === 201) {
                            if (data.data.err === -2) {
                                message.info(data.data.msg);
                                return
                            }
                            message.info('');
                            clearName();
                            setCurrentPage(2)
                            setTimeout(() => {
                                getAllUser(2, 5, 1)
                            }, 200)

                        }
                        else {
                            message.info('');
                            clearName();
                            return
                        }
                    }, () => {
                        return Promise.reject('')
                    }).catch(err => {
                        message.info(err)
                        clearName()
                    })
            }
        })
        if (!flag) { message.info('') }




    };

    const handleCancel = e => {
        setVisible(false)
        clearName()
    };

    //
    const showModal2 = (id) => {
        setVisible2(true)
        console.log('deleteId= ' + id)
        setDelUserId(id)

    };

    const handleOk2 = e => {
        setVisible2(false)
        console.log(delUserId)

        // -- 0:
        axios.post('/zjj/user/reduceRight',
            qs.stringify({
                _id: delUserId
            }),
            {
                headers: {
                    'content-type': 'application/x-www-form-urlencoded;charset=UTF-8',
                    'Accept': 'application/json;charset=UTF-8'
                }
            }
        ).then(
            (data) => {
                console.log(data)
                if (data.status === 200 || data.status === 201) {
                    if (data.data.err === -2) {
                        message.info(data.data.msg);
                        return
                    }
                    message.info('');
                    setCurrentPage(1)
                    setTimeout(() => {
                        getAllUser(2, 5, 1)
                    }, 200)

                }
                else {
                    message.info('');
                    return
                }
            }, () => {
                return Promise.reject('')
            }).catch(err => {
                message.info(err)
            })
    };

    const handleCancel2 = e => {
        setVisible2(false)
    };




    //
    const [currentPage, setCurrentPage] = useState(1)
    const paginationProps = {
        showSizeChanger: false,
        showQuickJumper: false,
        showTotal: () => `${total}`,
        pageSize: 5,
        current: currentPage,
        total: total,
        onShowSizeChange: (current, pageSize) => changePageSize(pageSize, current),
        onChange: (current) => changePage(current),
    }

    const changePageSize = (a, b) => {
        console.log(a, b)
    }

    const changePage = (v) => {
        console.log(v)
        setCurrentPage(v)
        getAllUser(0, 5, v)
    }

    // const paginationProps = {
    //     showSizeChanger: true,
    //     showQuickJumper: false,
    //     showTotal: () => `${totals}`,
    //     pageSize: this.state.pageSize,
    //     current: page.pageNum,
    //     total: page.total,
    //     onShowSizeChange: (current, pageSize) => this.changePageSize(pageSize, current),
    //     onChange: (current) => this.changePage(current),
    // }






    const columns = [

        {
            title: '',
            dataIndex: 'username',
            key: 'username',
            render: text => <span>{text}</span>,
        },
        {
            title: '',
            dataIndex: 'name',
            key: 'name',
            render: text => <span>{text}</span>,
        },
        {
            title: '',
            dataIndex: 'phone',
            key: 'phone',
        },
        {
            title: '',
            dataIndex: 'mail',
            key: 'mail',
        },
        {
            title: '',
            key: 'tags',
            dataIndex: 'tags',
            render: tags => (
                <span>
                    {tags.map(tag => {
                        let color = 'geekblue';

                        return (
                            <Tag color={color} key={tag}>
                                {tag.toUpperCase()}
                            </Tag>
                        );
                    })}
                </span>
            ),
        },
        {
            title: '',
            key: 'action',
            render: (text, record, index) => (
                <span>
                    {/* <span style={{ color: 'red' }}></span> */}
                    <span onClick={() => showModal2(record.key)} className='redLink' style={{ color: 'red', position: 'relative', left: '10px' }}></span>
                </span>
            ),
        },

    ];

    const data = []; // antd
    if (allUser.length > 0) {
        allUser.forEach((item, index) => {
            data.push({
                key: item._id,
                username: item.us ? item.us : '',
                name: item.name ? item.name : '',
                sex: item.sex === 1 ? '' : (item.sex === 2 ? '' : ''),
                phone: item.phone ? item.phone : '',
                mail: item.mail ? item.mail : '',
                tags: item.right === 0 ? [''] : (item.right === 1 ? [''] : (item.right === 2 ? [''] : (item.right === 3 ? [''] : ''))),

            })

        })
    }









    if (loading) {
        return <Spin className="myLoading" tip="..." size='large'>
            <div className='Right2'>
                <div>
                    <Modal
                        title=""
                        visible={visible}
                        onOk={handleOk}
                        onCancel={handleCancel}
                        destroyOnClose={true}
                    >
                        <div className='Right2-wrap__div'>
                            <Text style={{ fontSize: 'normal' }} className="Right2-wrap__text"></Text>
                            <Input
                                className="Right1-wrap__input"
                                placeholder=""
                                value={addType}
                                disabled={true}


                            />
                        </div>
                        <div className='Right2-wrap__div'>
                            <Text style={{ fontSize: 'normal' }} className="Right2-wrap__text"></Text>
                            <Input
                                className="Right2-wrap__input"
                                placeholder=""
                                value={username}
                                onChange={handleNameChange}
                            />
                        </div>



                    </Modal>
                </div>

                <div>
                    <Modal
                        title=''
                        visible={visible2}
                        onOk={handleOk2}
                        destroyOnClose={true}
                        onCancel={handleCancel2}
                    >
                        <p style={{ fontWeight: 'bold' }}>
                            
                    </p>
                    </Modal>
                </div>

                <ul className='Right2-ul'>
                    <li className='Right2-ul__li Right2-ul__li--posi'   >
                        <Button onClick={showModal} style={{ border: "none" }}>
                            <Icon className='Right2-ul__li--icon' type="plus-square" />
                            <Text className='addText' style={{ position: 'relative', left: '5px' }}></Text>
                        </Button>
                    </li>

                </ul>
                <div className='Right2-wrap'>
                    <Table columns={columns} dataSource={data} pagination={paginationProps} />
                </div>

            </div>
        </Spin>
    }
    return (
        <div className='Right2'>
            <div>
                <Modal
                    title=""
                    visible={visible}
                    onOk={handleOk}
                    onCancel={handleCancel}
                    destroyOnClose={true}
                >
                    <div className='Right2-wrap__div'>
                        <Text style={{ fontSize: 'normal' }} className="Right2-wrap__text"></Text>
                        <Input
                            className="Right2-wrap__input"
                            placeholder=""
                            value={addType}
                            disabled={true}


                        />
                    </div>
                    <div className='Right2-wrap__div'>
                        <Text style={{ fontSize: 'normal' }} className="Right2-wrap__text"></Text>
                        <Input
                            className="Right2-wrap__input"
                            placeholder=""
                            value={username}
                            onChange={handleNameChange}
                        />
                    </div>



                </Modal>
            </div>

            <div>
                <Modal
                    title=''
                    visible={visible2}
                    onOk={handleOk2}
                    destroyOnClose={true}
                    onCancel={handleCancel2}
                >
                    <p style={{ fontWeight: 'bold' }}>
                        
                    </p>
                </Modal>
            </div>

            <ul className='Right2-ul'>
                <li className='Right2-ul__li Right2-ul__li--posi'   >
                    <Button onClick={showModal} style={{ border: "none" }}>
                        <Icon className='Right2-ul__li--icon' type="plus-square" />
                        <Text className='addText' style={{ position: 'relative', left: '5px' }}></Text>
                    </Button>
                </li>

            </ul>
            <div className='Right2-wrap'>
                <Table columns={columns} dataSource={data} pagination={paginationProps} />
            </div>

        </div>
    )
}

export default React.memo(Right2)



