import React, { useState
    // , useContext
 } from 'react'
import { Typography, Input, Button, DatePicker,Select } from 'antd'
import {Link} from 'react-router-dom'
import '../checkUnusual/checkUnusual.scss'



const CheckUnusual = () => {
    const [username, setName] = useState(''); // reactHook  useStatestate
    const [title, setTitle] = useState(''); // reactHook  useStatestate
    const [time, setTime] = useState(''); // reactHook  useStatestate
    const [notice, setNotice] = useState(''); // reactHook  useStatestate
    const [type, setType] = useState(''); // reactHook  useStatestate


    const { Text } = Typography
    const { Option } = Select

    // 
    const handleTitleChange = (val) => {
        if(val.target.value.length>30){return}
        setTitle(val.target.value)
    }
    const handleNameChange = (val) => {
        if(val.target.value.length>30){return}
        setName(val.target.value)
    }
    const handleTimeChange = (val) => {
        if(val.target.value.length>5){return}
        setTime(val.target.value)
    }
    const handleNoticeChange = (val) => {
        if(val.target.value.length>140){return}
        setNotice(val.target.value)
    }
    const handleTypeChange = (val) => {
        setType(val)
    }
    const handleSubmit = () => {
        console.log(type)
    }

    return (
        <div className='checkUnusual'>
            <div className='navText'>
                <Text strong className='navFont'></Text>
            </div>
            <div className='checkUnusual-wrap'>
                <div className='checkUnusual-wrap__title'></div>
                <div className='checkUnusual-wrap__box'>
                    <div className='checkUnusual-wrap__div'>
                        <Text  className="checkUnusual-wrap__text"></Text>
                        <Input
                            className="checkUnusual-wrap__input"
                            placeholder=""
                            value={title}
                            onChange={handleTitleChange}
                        />
                    </div>
                    <div className='checkUnusual-wrap__div'>
                        <Text  className="checkUnusual-wrap__text"></Text>
                        <Input
                            className="checkUnusual-wrap__input"
                            placeholder=""
                            value={username}
                            onChange={handleNameChange}
                        />
                    </div>
                </div>
                
                <div className='checkUnusual-wrap__box'>
                    <div className='checkUnusual-wrap__div'>
                        <Text  className="checkUnusual-wrap__text"></Text>
                        <DatePicker className="checkUnusual-wrap__input"></DatePicker>
                        </div>
                    <div className='checkUnusual-wrap__div'>
                        <Text  className="checkUnusual-wrap__text"></Text>
                        <Input
                            className="checkUnusual-wrap__input"
                            placeholder=""
                            value={time}
                            type='number'
                            onChange={handleTimeChange}
                        />
                    </div>
                </div>

                <div className='checkUnusual-wrap__box' style={{marginTop:'25px',width:'50%'}}>
                    <div className='checkUnusual-wrap__div'>
                        <Text  className="checkUnusual-wrap__text"></Text>
                        <Select
                            className="checkUnusual-wrap__input"
                            placeholder=""
                            onChange={handleTypeChange}
                        >

                            <Option value='1'></Option>
                            <Option value='2'></Option>
                            <Option value='3'></Option>

                        </Select>
                    </div>
                    
                </div>

                    
                    <div className='checkUnusual-wrap__notice'>
                        <Text  className="checkUnusual-wrap__text"></Text>
                        <textarea
                            className="checkUnusual-wrap__textarea"
                            value={notice}
                            type='textarea'
                            onChange={handleNoticeChange}
                        />
                    </div>

                <div className='checkUnusual-wrap__bottom'>
                    <Button type='danger' className='checkUnusual-wrap__bottom--btn' onClick={handleSubmit}></Button>
                    <Link to='/index/requestProcess' style={{height:'100%'}}><Button className='checkUnusual-wrap__bottom--btn' ></Button></Link>
                </div>

            </div>



        </div>
    )
}

export default CheckUnusual
