//App.js  reducer

import React ,{useReducer} from 'react';
import AlertContext from "./alertContext";
import AlertReducer from "./alertReducer";
import { SET_ALERT,ClEAR_ALERT } from "../type";

const AlertState = props => {

    const initialState = {
        alert:null
    }; //state

    //useReducerreducer
    const [state,dispatch] = useReducer(AlertReducer,initialState)  //1reducer  2

    //
    const setAlert=(msg,type)=>{
        // setalert({msg,type}) //,alert
        // setTimeout(()=>{
        //   setalert(null) //2s
        // },2000)

        //dispatchuseState
        dispatch({
            type:SET_ALERT,   //reducer
            payload:{msg,type}   
        })

        setTimeout(()=>{
            dispatch({
                type:ClEAR_ALERT,
            })
        },3000)

      };

      
      // Providerapp.JS alertsetAlert
      return(
        <AlertContext.Provider value={{alert:state.alert,setAlert}}>
            {props.children}
        </AlertContext.Provider>   
        )

}

export default AlertState