import React ,{useReducer} from 'react';
import GithubContext from './githubContext';
import GithubReducer from './githubReducer';
import axios from 'axios'
import {SEARCH_USER,ClEAR_USER,GET_USERINFO,GET_REPOS,SET_LOADING} from '../type'

let githubClientId;
let githubClientSecret;

if(process.env.NODE_ENV !=="production"){ //
    githubClientId = process.env.REACT_APP_GITHUB_CLIENT_ID;
    githubClientSecret = process.env.REACT_APP_GITHUB_CLIENT_SECRET;
}else{
    githubClientId = process.env.GITHUB_CLIENT_ID;
    githubClientSecret = process.env.GITHUB_CLIENT_SECRET;

}

const GithubState = props =>{
    //
    const initialState = {
        users:[],
        loading:false,
        userInfo:{},
        repos:[]
    };
    //useReducerstateReducer
    const [state,dispatch] = useReducer(GithubReducer,initialState) //1reduver       2 
    //                                                          
    //search user
    const searchUser = async text =>{ //eserachinput
        setLoading()//loading
        const res = await axios.get(`https://api.github.com/search/users?q=${text}&client_id=
        ${githubClientId}$client_secret=
        ${githubClientSecret}`)
        dispatch({
            type:SEARCH_USER,
            payload:res.data.items //loadingstate
        })
      };
    //get userInfo
    const getUserInfo = async login =>{ //loginuser
        setLoading()//loading
        const res = await axios.get(`https://api.github.com/users/${login}?client_id=
        ${githubClientId}$client_secret=
        ${githubClientSecret}`)
        dispatch({
            type:GET_USERINFO,
            payload:res.data
        })
      };
    //get repos 
    const getRepos = async login =>{ //login
        setLoading()//loading
        const res = await axios.get(`https://api.github.com/users/${login}/repos?per_page=5&sort=created:asc&client_id=
        ${githubClientId}$client_secret=
        ${githubClientSecret}`)
        dispatch({
            type:GET_REPOS,
            payload:res.data
        })
      };
    //clear users
    const clearUser=()=>{
        dispatch({
            type:ClEAR_USER
        })
      }; 

    //set loading
    const  setLoading =()=>{
        dispatch({
            type:SET_LOADING  //dispatchReducer
        })
    }

    return (<GithubContext.Provider value={{
        users:state.users,
        userInfo:state.userInfo,
        repos:state.repos,
        loading:state.loading,
        setLoading,
        clearUser,
        getRepos,
        getUserInfo,
        searchUser
    }}>{props.children}</GithubContext.Provider>)


}
export default GithubState
