import React, { useEffect, useState, useContext, useRef } from 'react'
import { Spin,Typography, Table, Input, Button, Modal, message } from 'antd'
import { Link } from 'react-router-dom'
import Spinner from '../../../../laout/Spinner'
// import RightContext from '../../../../../context/right/rightContext'
import InfoContext from '../../../../../context/info/infoContext'
import moment from 'moment'
import axios from 'axios'
import qs from 'qs'

import '../InfoManage/InfoManage.scss'




const InfoManage = (e) => {

    //
    const [path,setPath] = useState('')
    const fileUpload = useRef(null) //
    const {TextArea} = Input
    const { Text } = Typography
    const [delName, setDelName] = useState('')//
    const [editContent, setEditContent] = useState('')//
    const [editId, setEditId] = useState('')//
    const [editTitle, setEditTitle] = useState('')//
    const [editPreview, setEditPreview] = useState('')//
    const [editTime, setEditTime] = useState('')//
    const [editAuthor, setEditAuthor] = useState('')//

    //context
    const infoContext = useContext(InfoContext)
    const { clearMess, getMess, mess = [], messCount = 0, loading } = infoContext


    //
    useEffect(() => {  //useEffect didMount
        getMess(5,1)
        return handleWillUnMount
        // eslint-disable-next-line
    }, [])

    const handleWillUnMount = () => {
        clearMess()
    }

    //
    const clearPath = () =>{
        setPath('')
    }

    //
    const clearEdit = () => {
        setEditContent('')
        setEditId('')
        setEditTitle('')
        setEditPreview('')
        setEditTime('')
        setEditAuthor('')
    }


    //
    const handleUpload = () => {

        let config = {
            headers: {
                'Content-Type': 'multipart/form-data'
            }
        }
        //
        if (fileUpload.current.files[0] === undefined) {
            message.info('!')
            return
        }

        //formData
        let formData = new FormData()
        formData.append('file', fileUpload.current.files[0])

        axios.post('/zjj/file/upload', formData, config).then(data => {
            if (data.status === 200) {
                if (data.data.err === -1) {
                    message.info('')
                    clearPath()
                    return
                }
                if (data.data.err === -2) {
                    message.info('')
                    clearPath()
                    return
                }
                // let link = 'http://l1nkkk.xyz:3001'
                let link = 'http://localhost:3001'
                let imgUrl = data.data.img

                console.log(imgUrl)
                // return updateImg(imgUrl)
                setPath(imgUrl)
                message.info('')

            }
        }).catch(err => {
            message.info(err)
            clearPath()
        })
       
        
    }
    //    
    const [visible, setVisible] = useState(false)
    const [visible2, setVisible2] = useState(false)

    //    
    const showModal = (id) => {
        setVisible(true)
        setDelName(id)
    };

    const handleOk = e => {
        setVisible(false)
        console.log('currentDel', delName)

        axios.post('/zjj/info/del', { _id:delName }).then(
            (data) => {
                if (data.status === 200) {
                    if (data.data.err === -1) {
                        message.info('!');
                        return
                    }
                    else if (data.data.err === -2) {
                        message.info('');
                        return
                    }
                    else {
                        message.info('~');
                        setTimeout(() => {
                            getMess(5,1)
                        }, 200)
                    }
                }
            })
    };

    const handleCancel = e => {
        setVisible(false)
    };



    //
    const showModal2 = (id) => {
        setVisible2(true)
        mess.forEach(e => {
            if (e._id === id) {
                setEditContent(e.content)
                setEditId(e._id)
                setEditTitle(e.title)
                setEditPreview(e.img)
                setEditTime(e.time)
                setEditAuthor(e.author)
            }
        })



    };


    // 
    const handleContentChange = (val) => {
        // if (val.target.value.length > 140) { return }
        setEditContent(val.target.value)
    }
    const handleTitleChange = (val) => {
        if (val.target.value.length > 30) { return }
        setEditTitle(val.target.value)
    }

    //
    const handleOk2 = e => { //
        setVisible2(false)

        if (!editContent || !editId || !editPreview || !editTitle ) {
            message.info('')
            clearEdit()
            return
        }
        if (editTitle.length > 30) {
            message.info('30')
            clearEdit()
            return
        }
        // if (editContent.length > 140) {
            // message.info('30')
            // clearEdit()
            // return
        // }


        //
        axios.post('/zjj/info/updateInfo',
            qs.stringify({
                content:editContent,
                _id:editId,
                title:editTitle,
                img:path?path:editPreview,
                time:editTime,
                author:editAuthor,
            }),
            {
                headers: {
                    'content-type': 'application/x-www-form-urlencoded;charset=UTF-8',
                    'Accept': 'application/json;charset=UTF-8'
                }
            }
        ).then(
            (data) => {
                console.log(data)
                if (data.status !== 200) {
                    message.info('')
                    clearEdit()
                    return
                }
                if (data.data.err === -2) {
                    message.info(data.data.msg)
                    clearEdit()
                    return
                }
                if (data.data.err === -1) {
                    message.info(data.data.msg)
                    clearEdit()
                    return
                }
                //
                clearEdit();
                setCurrentPage(1)
                message.info('');
                setTimeout(() => {
                    getMess(5, 1)
                }, 200)
            }, () => {
                return Promise.reject('')
            }).catch(err => {
                message.info(err)
                console.log(err)
                clearEdit()
            })
    };

    const handleCancel2 = e => {
        setVisible2(false)
        clearEdit()
    };



    //
    const [currentPage, setCurrentPage] = useState(1)
    const paginationProps = {
        showSizeChanger: false,
        showQuickJumper: false,
        showTotal: () => `${messCount}`,
        pageSize: 5,
        current: currentPage,
        total: messCount,
        onShowSizeChange: (current, pageSize) => changePageSize(pageSize, current),
        onChange: (current) => changePage(current),
    }

    const changePageSize = (a, b) => {
        console.log(a, b)
    }

    const changePage = (v) => {
        console.log(v)
        setCurrentPage(v)
        getMess(5,v)
    }






    const columns = [
        {
            title: '',
            dataIndex: 'title',
            key: 'title',
        },
        {
            title: '',
            dataIndex: 'date',
            key: 'date',
        },
        {
            title: '',
            dataIndex: 'name',
            key: 'name',
        },


        {
            title: '',
            key: 'action',
            render: (text, record, index) => (
                <span>
                    {/* <Link to={`/txt/infoEdit/${record.key}`}><span className='redLink' style={{ color: 'red' }}></span></Link> */}
                    <span onClick={() => { showModal2(record.key) }} className='redLink' style={{ color: 'red' }}></span>
                    <span onClick={() => showModal(record.key)} className='redLink' style={{ color: 'red', position: 'relative', left: '10px' }}></span>
                </span>
            ),
        },
    ];

    const data = [];
    if (mess.length >0) {
        mess.forEach((item, index) => {
            data.push({
                key: item._id,
                title: item.title ? item.title : '',
                date: item.time ? moment(Number(item.time)).format('YYYY-MM-DD') : '',
                name: item.author ? item.author : '',
            })
        })
    }



    if (loading) { return  <Spin className="myLoading" tip="..."  size='large'>
          <div className='InfoManage'>
            <div>
                <Modal
                    title=''
                    visible={visible}
                    onOk={handleOk}
                    onCancel={handleCancel}
                >
                    <p style={{ fontWeight: 'bold' }}>
                        
                    </p>
                </Modal>
            </div>
            <div>
                <Modal
                    title=""
                    visible={visible2}
                    onOk={handleOk2}
                    onCancel={handleCancel2}
                    destroyOnClose={true}
                >
                    <div className='TeamManage-wrap__div'>
                        <Text style={{ fontSize: 'normal' }} className="List-wrap__text"></Text>
                        <Input
                            style={{ marginBottom: '10px' }}
                            className="TeamManage-wrap__input"
                            placeholder=""
                            value={editTitle}
                            onChange={handleTitleChange}
                        />
                    </div>

                    <div    className='TeamManage-wrap__div'
                            style={{
                                marginBottom: '10px'
                            }}
                            >
                        <Text style={{ fontSize: 'normal' }} className="List-wrap__text"></Text>
                        <input type="file" ref={fileUpload} className='InfoManage-wrap__input' />
                        <Button className='InfoManage-btn' type='danger' onClick={handleUpload}></Button>

                    </div>

                    <div className='TeamManage-wrap__div'
                            style={{
                                marginBottom: '10px'
                            }}
                    >
                           <Text style={{ fontSize: 'normal' }} className="List-wrap__text"></Text>
                        <TextArea
                            style={{width:'83%' ,height:'80px'}}
                            
                            className="TeamManage-wrap__notice"
                            placeholder=""
                            value={editContent}
                            onChange={handleContentChange}
                        />
                    </div>

                </Modal>
            </div>
            <div className='navText'>
                <Text strong style={{ color: '#ff0000' }} className='navFont'></Text>
            </div>
            <ul className='InfoManage-ul'>
                <li className='InfoManage-ul__li'>
                    <Link to='/txt/infoAdd'><Button className='InfoManage-ul__li--link' style={{ fontSize: '15px' }}></Button></Link>
                </li>


            </ul>
            <div className='InfoManage-wrap'>
                <Table columns={columns} dataSource={data} pagination={paginationProps} />
            </div>
        </div>
    </Spin> }
    return (
        <div className='InfoManage'>
            <div>
                <Modal
                    title=''
                    visible={visible}
                    onOk={handleOk}
                    onCancel={handleCancel}
                >
                    <p style={{ fontWeight: 'bold' }}>
                        
                    </p>
                </Modal>
            </div>
            <div>
                <Modal
                    title=""
                    visible={visible2}
                    onOk={handleOk2}
                    onCancel={handleCancel2}
                    destroyOnClose={true}
                >
                    <div className='TeamManage-wrap__div'>
                        <Text style={{ fontSize: 'normal' }} className="List-wrap__text"></Text>
                        <Input
                            style={{ marginBottom: '10px' }}
                            className="TeamManage-wrap__input"
                            placeholder=""
                            value={editTitle}
                            onChange={handleTitleChange}
                        />
                    </div>

                    <div    className='TeamManage-wrap__div'
                            style={{
                                marginBottom: '10px'
                            }}
                            >
                        <Text style={{ fontSize: 'normal' }} className="List-wrap__text"></Text>
                        <input type="file" ref={fileUpload} className='InfoManage-wrap__input' />
                        <Button className='InfoManage-btn' type='danger' onClick={handleUpload}></Button>

                    </div>

                    <div className='TeamManage-wrap__div'
                            style={{
                                marginBottom: '10px'
                            }}
                    >
                           <Text style={{ fontSize: 'normal' }} className="List-wrap__text"></Text>
                        <TextArea
                            style={{width:'83%' ,height:'80px'}}
                            
                            className="TeamManage-wrap__notice"
                            placeholder=""
                            value={editContent}
                            onChange={handleContentChange}
                        />
                    </div>

                </Modal>
            </div>
            <div className='navText'>
                <Text strong style={{ color: '#ff0000' }} className='navFont'></Text>
            </div>
            <ul className='InfoManage-ul'>
                <li className='InfoManage-ul__li'>
                    <Link to='/txt/infoAdd'><Button className='InfoManage-ul__li--link' style={{ fontSize: '15px' }}></Button></Link>
                </li>


            </ul>
            <div className='InfoManage-wrap'>
                <Table columns={columns} dataSource={data} pagination={paginationProps} />
            </div>

        </div>
    )
}

export default React.memo(InfoManage)
