import React, { useEffect, useState, useContext, useRef } from 'react'
import { Spin,Typography, Button,Table, Input, Tag, Modal, message } from 'antd'
// import { Link } from 'react-router-dom'

import Spinner from '../../../../laout/Spinner'
// import RightContext from '../../../../../context/right/rightContext'
import picDefault from '../../../../../pic/38.png'
import iconDefault from '../../../../../pic/20.png'
import axios from 'axios'
import qs from 'qs'
import InfoContext from '../.././../../../context/info/infoContext'

import '../PicManage/PicManage.scss'




const PicManage = (e) => {


    //
    const { Text } = Typography
    const fileUpload = useRef(null) //
    const [path,setPath] = useState('') //


    //context
    const infoContext = useContext(InfoContext)
    const { getAllPic, pic = [], loading } = infoContext
    const [delId, setDelId] = useState('')

    //
    useEffect(() => {  //useEffect didMount
        getAllPic()

        return handleUnMount
        // eslint-disable-next-line
    }, [])

    
    //
    const clearPath =()=>{
        setPath('')
    }

    //
    const handleUnMount = () =>{
        clearPath()
    }

    //    
    const [visible, setVisible] = useState(false)
    const [visible1, setVisible1] = useState(false)

    //
    const showModal1 = (id) => {
        setVisible1(true)
        setDelId(id)
    };

    const handleOk1 = e => {
        setVisible1(false)
        //
        axios.post('/zjj/pic/del', { _id:delId }).then(
            (data) => {
                if (data.status === 200) {
                    if (data.data.err === -1) {
                        message.info('!');
                        return
                    }
                    else if (data.data.err === -2) {
                        message.info('');
                        return
                    }
                    else {
                        message.info('~');
                        setTimeout(() => {
                            getAllPic()
                        }, 200)
                    }
                }
            })
        // axios.delete(`/AutomaticOfficeSystem/announcement/api/v1/shufflingFigure?id=${delId}`,{}).then(data => {
        //     if (data.status === 200 || data.status === 204) {
        //         message.info('~')
        //         setTimeout(()=>{
        //             getPic()
        //         },200)
        //     }
        //     else {
        //         message.info('');
        //         return
        //     }
        // }).catch(err => {
        //     message.info(err)
        // })
    };

    const handleCancel1 = e => {
        setVisible1(false)
    };

    //
    const showModal = () => {
        setVisible(true)
    };



    //
    const handleUpload = () => {

        console.log(fileUpload.current.files[0])
        let config = {
            headers: {
                'Content-Type': 'multipart/form-data'
            }
        }
        if (fileUpload.current.files[0] === undefined) {
            message.info('')
            return
        }

        //formData
        let formData = new FormData()
        formData.append('file', fileUpload.current.files[0])


        axios.post('/zjj/file/upload', formData, config).then(data => {
            if (data.status === 200) {
                if (data.data.err === -1) {
                    message.info('')
                    clearPath()
                    return
                }
                if (data.data.err === -2) {
                    message.info('')
                    clearPath()
                    return
                }
                // let link = 'http://l1nkkk.xyz:3001'
                let link = 'http://localhost:3001'
                let imgUrl = data.data.img

                console.log(imgUrl)
                // return updateImg(imgUrl)
                setPath(imgUrl)
                message.info('')

            }
        }).catch(err => {
            message.info(err)
            clearPath()
        })
    }



    //
    const handleOk = e => {
        setVisible(false)
        if(!path){
            message.info('')
            return
        }

        axios.post('/zjj/pic/addPic', { path }).then(
            (data) => {
                if (data.status === 200) {
                    if (data.data.err === -1) {
                        message.info('');
                        clearPath()
                        return
                    }
                    else if (data.data.err === -2) {
                        message.info(',');
                        clearPath()
                        return
                    }
                    else {
                        message.info('~');
                        clearPath()
                        setTimeout(() => {
                            getAllPic()
                        }, 200)
                    }
                }
            })

        

        

    };

    const handleCancel = e => {
        setVisible(false)
        clearPath()
    };



    if(loading){return  <Spin className="myLoading" tip="..."  size='large'>
        <div className='PicManage'>
            <div>
                <Modal
                    style={{zIndex:999}}
                    title=''
                    visible={visible}
                    onOk={handleOk}
                    onCancel={handleCancel}
                    destroyOnClose={true}
                >
                    <input type="file" ref={fileUpload} className='PicManage-wrap__input' />
                    <Button className='PicManage-btn' onClick={handleUpload} type='danger' ></Button>
                </Modal>
            </div>
            <div>
                <Modal
                    title=''
                    visible={visible1}
                    onOk={handleOk1}
                    onCancel={handleCancel1}
                >
                    <p style={{ fontWeight: 'bold' }}>
                        
                    </p>
                </Modal>
            </div>
            <div className='navText'>
                <Text strong style={{ color: '#ff0000' }} className='navFont'></Text>
                <Button className='PicManage-btn  PicManage-addBtn' onClick={showModal} type='danger'></Button>

            </div>

            <div className='PicManage-wrap' style={{ overflowY: 'scroll', paddingBottom: '80px' }}>
                {pic.length>0 && pic.map(e=>(

                <div key={e._id} className='PicManage-wrap__div'>
                    <div className='PicManage-wrap__divLeft'>
                        <img  className='redLink' style={{ width: '100px', height: '100px' }} src={iconDefault} alt="default" />
                        <Text style={{ color: 'red', position: 'relative', left: '5px' }}>1920*1080</Text>
                    </div>
                    <div className='PicManage-wrap__divRight' style={{flex:'1'}}>
                        <img style={{ position:'relative',left:'40px',height: '120px',width:'350px' }} src={e.img?e.img:picDefault} alt="pic" />
                    </div>
                    <div className='PicManage-wrap__divRight' >
                        <Text onClick={()=>showModal1(e._id)} className='redLink' style={{ color: 'red', position: 'relative', left: '5px' }}></Text>
                    </div>
                </div>
    ))}   
            </div>
        </div>
    </Spin>}
    return (
        <div className='PicManage'>
            <div>
                <Modal
                    style={{zIndex:999}}
                    title=''
                    visible={visible}
                    onOk={handleOk}
                    onCancel={handleCancel}
                    destroyOnClose={true}
                >
                    <input type="file" ref={fileUpload} className='PicManage-wrap__input' />
                    <Button className='PicManage-btn' onClick={handleUpload} type='danger' ></Button>
                </Modal>
            </div>
            <div>
                <Modal
                    title=''
                    visible={visible1}
                    onOk={handleOk1}
                    onCancel={handleCancel1}
                >
                    <p style={{ fontWeight: 'bold' }}>
                        
                    </p>
                </Modal>
            </div>
            <div className='navText'>
                <Text strong style={{ color: '#ff0000' }} className='navFont'></Text>
                <Button className='PicManage-btn  PicManage-addBtn' onClick={showModal} type='danger'></Button>

            </div>

            <div className='PicManage-wrap' style={{ overflowY: 'scroll', paddingBottom: '80px' }}>
                {pic.length>0 && pic.map(e=>(

                <div key={e._id} className='PicManage-wrap__div'>
                    <div className='PicManage-wrap__divLeft'>
                        <img  className='redLink' style={{ width: '100px', height: '100px' }} src={iconDefault} alt="default" />
                        <Text style={{ color: 'red', position: 'relative', left: '5px' }}>1920*1080</Text>
                    </div>
                    <div className='PicManage-wrap__divRight' style={{flex:'1'}}>
                        <img style={{ position:'relative',left:'40px',height: '120px',width:'350px' }} src={e.img?e.img:picDefault} alt="pic" />
                    </div>
                    <div className='PicManage-wrap__divRight' >
                        <Text onClick={()=>showModal1(e._id)} className='redLink' style={{ color: 'red', position: 'relative', left: '5px' }}></Text>
                    </div>
                </div>
    ))}   


            </div>

        </div>
    )
}

export default React.memo(PicManage)
