import React, { useState
    // , useContext
 } from 'react'
import { Typography, Input, Button, DatePicker,Select } from 'antd'
import {Link} from 'react-router-dom'

import '../vacationReq/vacationReq.scss'



const VacationReq = (e) => {
    const [username, setName] = useState(''); // reactHook  useStatestate
    const [title, setTitle] = useState(''); // reactHook  useStatestate
    const [time, setTime] = useState(''); // reactHook  useStatestate
    const [notice, setNotice] = useState(''); // reactHook  useStatestate
    const [type, setType] = useState(''); // reactHook  useStatestate
    const [work, setWork] = useState(''); // reactHook  useStatestate

    // const { MonthPicker, RangePicker } = DatePicker;
    const { Option } = Select
    const { Text } = Typography
    // 
    const handleTitleChange = (val) => {
        if(val.target.value.length>30){return}
        setTitle(val.target.value)
    }
    const handleNameChange = (val) => {
        if(val.target.value.length>30){return}
        setName(val.target.value)
    }
    const handleTimeChange = (val) => {
        if(val.target.value.length>5){return}
        setTime(val.target.value)
    }
    const handleNoticeChange = (val) => {
        if(val.target.value.length>140){return}
        setNotice(val.target.value)
    }
    const handleTypeChange = (val) => {
        setType(val)
    }
    const handleWorkChange = (val) => {
        if(val.target.value.length>25){return}
        setWork(val.target.value)
    }
    const handleSubmit = () => {
        console.log(type,username)
    }



    return (
        <div className='vacationReq'>
            <div className='navText'>
                <Text strong className='navFont'></Text>
            </div>

            <div className='vacationReq-wrap'>
                <div className='vacationReq-wrap__title'></div>
                <div className='vacationReq-wrap__box'>
                    <div className='vacationReq-wrap__div'>
                        <Text  className="vacationReq-wrap__text"></Text>
                        <Input
                            className="vacationReq-wrap__input"
                            placeholder=""
                            value={title}
                            onChange={handleTitleChange}
                        />
                    </div>
                    <div className='vacationReq-wrap__div'>
                        <Text  className="vacationReq-wrap__text"></Text>
                        <Input
                            className="vacationReq-wrap__input"
                            placeholder=""
                            value={username}
                            onChange={handleNameChange}
                        />
                    </div>
                </div>
                
                <div className='vacationReq-wrap__box'>
                    <div className='vacationReq-wrap__div'>
                        <Text  className="vacationReq-wrap__text"></Text>
                        <DatePicker className="vacationReq-wrap__input"></DatePicker>
                        </div>
                    <div className='vacationReq-wrap__div'>
                        <Text  className="vacationReq-wrap__text"></Text>
                        <Input
                            className="vacationReq-wrap__input"
                            placeholder=""
                            value={time}
                            type='number'
                            onChange={handleTimeChange}
                        />
                    </div>
                </div>

                <div className='vacationReq-wrap__box' style={{marginTop:'25px'}}>
                    <div className='vacationReq-wrap__div'>
                        <Text  className="vacationReq-wrap__text"></Text>
                        <Select
                            className="vacationReq-wrap__input"
                            placeholder=""
                            onChange={handleTypeChange}
                        >

                            <Option value='1'></Option>
                            <Option value='2'></Option>
                            <Option value='3'></Option>

                        </Select>
                    </div>
                    <div className='vacationReq-wrap__div' >
                        <Text  className="vacationReq-wrap__text"></Text>
                        <Input
                            className="vacationReq-wrap__input"
                            placeholder=""
                            value={work}
                            onChange={handleWorkChange}
                        />
                    </div>
                </div>

                    
                    <div className='vacationReq-wrap__notice'>
                        <Text  className="vacationReq-wrap__text"></Text>
                        <textarea
                            className="vacationReq-wrap__textarea"
                            value={notice}
                            type='textarea'
                            onChange={handleNoticeChange}
                        />
                    </div>

               
                <div className='vacationReq-wrap__bottom'>
                    <Button type='danger' className='vacationReq-wrap__bottom--btn' onClick={handleSubmit}></Button>
                    <Link to='/index/requestProcess' style={{height:'100%'}}><Button className='vacationReq-wrap__bottom--btn' ></Button></Link>
                </div>

            </div>
                

        </div>
    )
}

export default VacationReq
