import React, { useReducer } from 'react';
import InfoContext from './infoContext';
import InfoReducer from './infoReducer';
import axios from 'axios'
import { message } from 'antd'

import { GET_INFO,CLEAR_MESS,GET_MESS, GET_PIC, SET_LOADING } from '../type'



const InfoState = props => {
    //
    const initialState = {
        pic: [], // 
        loading: false,

        mess: [], //
        messCount: 0, //

        info: [] , // --  -- 

        title:'',
        content:'',// 



    };

    //useReducerstateReducer
    const [state, dispatch] = useReducer(InfoReducer, initialState) //1reduver       2 

    //                                                          
    //
    const getAllPic = async () => {
        setLoading(true)//loading   

        let res = null
        try {
            res = await axios.get('/zjj/pic/getAllPic', {})
        }
        catch (err) {
            message.info(err)
            return Promise.reject()
        }
        if (res.status !== 200) {
            message.info('')
            setLoading(false)
            return
        }
        if (res.data.err === -2) {
            message.info('')
            setLoading(false)
            return
        }
        dispatch({
            type: GET_PIC,
            payload: {
                pic: res.data.list,
            }
        })
        setLoading(false)

    }


    //set loading
    const setLoading = (loading) => {
        dispatch({
            type: SET_LOADING, //dispatchReducer
            payload: { loading }
        })
    }

    //
    const getMess = async (limit, page) => { //loginuser
        setLoading(true)//loading
        const res = await axios.post(`/zjj/info/getAllInfo`,  {limit,page})

        if (res.data.err === -2) {
            message.info('')
            setLoading(false)
            return
        }
        console.log(res.data.info.list,'ddd')
        dispatch({
            type: GET_MESS,
            payload: {
                mess: res.data.info.list,
                messCount: res.data.info.count,
            }
        })
        setLoading(false)

    };



    // -- 
    const getInfo = async (index) => { //loginuser
        setLoading(true)//loading
        const res = await axios.get(`/zjj/info/getInitialInfo`,  {})
        if (res.data.err === -2) {
            message.info('')
            setLoading(false)
            return
        }

        //
        if(index){
            dispatch({
                type: GET_INFO,
                payload: {
                    info: res.data.list,
                    title: res.data.list[index-1].title,
                    content: res.data.list[index-1].content
                }
            })
        }else{
            dispatch({
                type: GET_INFO,
                payload: {
                    info: res.data.list,
                    title : '',
                    content: ''
                }
            })
        }

       
       
        setLoading(false)

    };


    //clearMess
    const clearMess = () => {
        dispatch({
            type: CLEAR_MESS
        })
    };


    return (<InfoContext.Provider value={{
        pic: state.pic,   //
        loading: state.loading,

        mess: state.mess, //
        messCount: state.messCount,

        info: state.info, //
        title: state.title,
        content: state.content,

        clearMess,
        getInfo,
        getMess,
        setLoading,
        getAllPic,
    }}>{props.children}</InfoContext.Provider>)


}
export default InfoState
