//state

import {SET_ALERT,ClEAR_ALERT} from '../type'

export default (state,action)=>{
    switch (action.type) {  //actiondispatch
     case SET_ALERT:
         return {
             ...state,
             alert:action.payload
         };  //payload
     case ClEAR_ALERT:
         return {
             ...state,
             alert:null
         };    
     default:
         return state;
    
    
    }
}