import React, { useEffect, useContext } from 'react'
import { NavLink } from 'react-router-dom'  //a
import { Typography, Button } from 'antd'
import { Link } from 'react-router-dom'
import UserContext from '../../../../../context/user/userContext'
import Spinner from '../../../../laout/Spinner'

import '../Account/Account.scss'





const Account = (e) => {
    const userContext = useContext(UserContext)
    const { getLoginUser, loading, list } = userContext
    

    useEffect(() => {  //useEffect didMount
        let _id = localStorage.getItem('_id')
        getLoginUser(_id)

        // eslint-disable-next-line
    }, [])


    //
    const { name, sex, mail, phone,img } = list



    const { Text } = Typography
    const imgPre = require('../../../../../pic/27.png')



    // if (loading) {
    //     return (<Spinner></Spinner>)
    // }   
    //peopleloading

    return (

        <div className='Account'>
            <div className='navText'>
                <Text strong style={{ color: '#ff0000' }} className='navFont'></Text>
            </div>
            <ul className='Account-ul'>
                <li className='Account-ul__li'><NavLink exact activeClassName="Account-ul__li--active" className='Account-ul__li--link' to='/people'></NavLink></li>
                <li className='Account-ul__li'><NavLink activeClassName="Account-ul__li--active" className='Account-ul__li--link' to='/people/pwdManage'></NavLink></li>

            </ul>
            <div className='Account-wrap'>
                <div className='Account-wrap__box' style={{marginBottom:'20px'}}>
                    <div className='Account-wrap__div' style={{ justifyContent: 'flex-start' }}>
                        <Text className="Account-wrap__text Account-wrap__lr"></Text>
                        <Text className='Account-wrap__textOld'>{name ? name : ''}</Text>
                    </div>
                    <div className='checkUnusual-wrap__div' style={{ justifyContent: 'flex-start' }}>
                        <Text className="checkUnusual-wrap__text Account-wrap__lr"></Text>
                        <Text className='Account-wrap__textOld'>{sex ? (sex===1?'':'') : ''}</Text>

                    </div>
                </div>
                <div className='Account-wrap__box'>
                    <div className='Account-wrap__div' style={{ justifyContent: 'flex-start' }}>
                        <Text className="Account-wrap__text Account-wrap__lr"></Text>
                        <Text className='Account-wrap__textOld'>{mail ? mail : ''}</Text>

                    </div>
                    <div className='Account-wrap__div' style={{ justifyContent: 'flex-start' }}>
                        <Text className="Account-wrap__text Account-wrap__lr"></Text>
                        <Text className='Account-wrap__textOld'>{phone ? phone : ''}</Text>

                    </div>
                </div>

                <div className='Account-wrap__box' style={{ width: '50%', marginTop: '25px' }}>
                    <div className='Account-wrap__div' style={{ justifyContent: 'flex-start',position:'relative',top:'10px' }}>
                        <Text className="Account-wrap__text  Account-wrap__lr"></Text>
                        <div className='Account-wrap__iconDiv'>
                            {!img && (<img
                                className='Account-wrap__icon'
                                src={imgPre}
                                alt='pic'
                            />)}
                              {img && (<img
                                className='Account-wrap__icon'
                                src={img}
                                alt='pic'
                            />)}

                        </div>
                    </div>
                </div>

                <div className='Account-wrap__bottom'>
                    <Link to='/people/accountEdit' style={{ height: '100%' }}><Button type='danger' className='Account-wrap__bottom--btn' ></Button></Link>
                </div>
            </div>

        </div>
    )
}

export default Account
