import React, { useState, useContext, useEffect } from 'react'
import { Button, Input, Typography, message, Icon } from 'antd'
import axios from 'axios'
import AlertContext from '../../context/alert/alertContext'
import qs from 'qs'

const LoginBack = (props) => {

    const alertContext = useContext(AlertContext) //contextstate 
    const [username, setName] = useState(''); // reactHook  useStatestate
    const [password, setPass] = useState(''); // reactHook  useStatestate
    const { Text } = Typography


    useEffect(() => {  //useEffect didMount -- 
        let name = localStorage.getItem('name')
        let token = localStorage.getItem('token')
        console.log(name, token)
       
    }, [])

    const clearData = () => {
        setName('')
        setPass('')
    }

    // 
    const handleGoLoginBack = () => {
        props.history.replace('/login')
    }


    // 
    const handleLoginBack = (e) => {
            let us = username,
                ps = password
            if (!us || !ps) {
                message.info('');
                return
            };
            // eslint-disable-next-line
            axios.post('/zjj/user/loginBack', { us, ps }).
                then((data) => {
                    if (data.status === 200) {
                        if (data.data.err === -3) {
                            message.info('');
                            clearData();
                            return

                        }
                        else if (data.data.err === -5) {
                            message.info('');
                            clearData();
                            return
                        }
                        else if(data.data.err === -6){
                            message.info('')
                            clearData()
                            return
                        }
                        else {
                            if (data.data.msg) {
                                localStorage.setItem('name', username) //
                                localStorage.setItem('login', data.data.msg.login)
                                localStorage.setItem('_id', data.data.data[0]._id)
                                localStorage.setItem('right', data.data.data[0].right) //1--0--
                                localStorage.setItem('mode', '2')
                                console.log('login', data)
                            }
    
    
                            props.history.push({ pathname: '/indexBack' })
                            alertContext.setAlert(",", "dark") //
                        }
                    }
                })
                .catch((err) => { console.log(err) });
        }

    // 
    const handleNameChange = (val) => {
        setName(val.target.value)
    }
    const handlePassChange = (val) => {
        setPass(val.target.value)
    }


    return (
        <div className="page-login"
            style={{ backgroundSize: 'cover', width: '100%', height: '100%', backgroundImage: "url(" + require("../../pic/img2.jpg") + ")" }}
        >
            {/* <Logo/> */}
            <div className='login-wrap'>
                <div><Text strong className='linkFont'></Text></div>
                <span className='rootFont' ></span>
                <Input
                    className='login-name loginInp'
                    value={username}
                    placeholder=""
                    onChange={handleNameChange}
                />
                <span className="login-span rootFont"></span>
                <Input
                    className="loginInp"
                    type="password"
                    placeholder=""
                    value={password}
                    onChange={handlePassChange}
                />

                <div className='login-btn'><Button type="primary"
                    onClick={handleLoginBack}
                ></Button></div>
                <div className='hover-btn'><Icon type='login' className='login-icon' /><span className='loginFont'
                    onClick={handleGoLoginBack}
                ></span></div>
            </div>
        </div>
    )
}






export default LoginBack

