import React, { useReducer } from 'react';
import FileContext from './fileContext';
import FileReducer from './fileReducer';
import axios from 'axios'
import qs from 'qs'
import { message } from 'antd'

import { GET_SEARCH_FILE,GET_FILE,SET_MODE,SET_LOADING } from '../type'



const FileState = props => {
    //
    const initialState = {
        loading: false,
        mode: false, //false --  true -- 
        file: [], //
        count: 0, //
        _file: [], //
        _count: 0, //
        val: '', //
        title: '', //
    
    };

    //useReducerstateReducer
    const [state, dispatch] = useReducer(FileReducer, initialState) //1reduver       2 

    //                                                          
    
   
    //getFile ,
    const getFile = async (type,limit,page) => { //loginuser
        setLoading(true)//loading
        const res = await axios.post('/zjj/myFile/getFileByType',{type,limit,page})
        if(res.data.err===-1){
            message.info('')
            setLoading(false)
            return
        }
        if(res.data.err===-2){
            message.info('')
            setLoading(false)
            return
        }
        dispatch({
            type: GET_FILE,
            payload: {
                file: res.data.info.list,
                count: res.data.info.count,
            }
        })
        setLoading(false)
        setMode(false)

    };
  

     //getSearchTable ,
     const _getFile = async (Kw,limit,page) => { //loginuser
        setLoading(true)//loading
        setMode(true)//

        const res = await axios.post('/zjj/myFile/getFileByKw',{Kw,limit,page})
        if(res.data.err===-1){
            message.info('')
            setLoading(false)
            return
        }
        if(res.data.err===-2){
            message.info('')
            setLoading(false)
            return
        }
        dispatch({
            type: GET_SEARCH_FILE,
            payload: {
                _file: res.data.info.list,
                _count: res.data.info.count,
                val: Kw
            }
        })
        setLoading(false)
    };

    


    //set loading
    const setLoading = (loading) => {
        dispatch({
            type: SET_LOADING, //dispatchReducer
            payload: { loading }
        })
    }

    //set mode : false ===  ;   true === 
    const setMode = (mode) => {
        dispatch({
            type: SET_MODE, 
            payload: { mode }
        })
    }

    return (<FileContext.Provider value={{
        //
        loading: state.loading,
        mode: state.mode,
        file: state.file,
        _file: state._file,
        count: state.count,
        _count: state._count,
        val: state.val,
        title: state.title,
        
        //
        setMode,
        setLoading,
        getFile,
        _getFile,

    }}>{props.children}</FileContext.Provider>)


}
export default FileState
