import React ,{useEffect}from 'react'
import PropTypes from 'prop-types'
import { Link,NavLink } from 'react-router-dom'  //a
import { Menu, Dropdown, Icon, message,Popconfirm } from 'antd'
import axios from 'axios'

const Navbar = (props) => {
    const {  title } = props
    let name = localStorage.getItem('name')
    let login = localStorage.getItem('login')
    let mess = login ? name+'' : ''
    let _id =localStorage.getItem('_id') 

    useEffect(()=>{
        if(!login){
            message.info('')
            setTimeout(()=>{
                window.location.href = '/'
            },1000)
        }
        else{
            checkToken(_id)  //
        }
    },[])

    //token
    const checkToken =  (_id) => {
        axios.post('/zjj/user/checkBack', {_id},
        {
            headers: {
                'username': name,
            }
        }
        ).then((data) => {
            if (data.status !== 200) {
                message.info('')
                return
            }
            if (data.data.err === -999) {
             return Promise.reject('!')
            }
            if (data.data.err === -2) {
                return Promise.reject('!')
            }
            if (data.data.err === -1) {
                return Promise.reject('')
            }

        })
            .catch((err) => {
                console.log(err);
                message.info(err);
                localStorage.removeItem('name') //
                localStorage.removeItem('login')
                localStorage.removeItem('mode')
                localStorage.removeItem('_id')
                localStorage.removeItem('right') //1--0--

                setTimeout(() => {
                    window.location.href = '/'
                }, 1000)
            });
    }

    const logOut = () => { //sessionId
        axios.post('/zjj/user/logOut', {}).then((data) => {
            console.log(data, login)
            if (data.status === 200) {
                if (data.data.err === 0) {
                    //
                    message.info('');
                    localStorage.removeItem('name') //
                    localStorage.removeItem('login')
                    localStorage.removeItem('mode')
                    localStorage.removeItem('_id')
                    localStorage.removeItem('right') //1--0--

                    setTimeout(() => {
                        // props.history.push({ pathname: '/' })
                        window.location.href = '/'
                    }, 1000)

                } else {
                    message.info('');
                    return false
                }
            }
        })
    }

    const cancel = () => {
        message.info('')
    }

    const menu = (
        <Menu>
            <Menu.Item>

                {login &&
                    <Popconfirm
                        title=""
                        // width={100}
                        style={{width:'1000px'}}
                        onConfirm={logOut}
                        onCancel={cancel}
                        okText="Yes"
                        cancelText="No"
                    >
                        <span href="#"></span>
                    </Popconfirm>
                }

                {!login && <Link to="/">
                    
                </Link>}
            </Menu.Item>
        </Menu>
    );

    return (
        <nav className="navbar App-header">
            <div className='nav-color'>
                {title}
            </div>
            <ul className="nav_ul">
                <li><NavLink className="link" activeClassName="nav_active" to="/indexBack"></NavLink></li>
                <li><NavLink className="link" activeClassName="nav_active" to="/authority"></NavLink></li>
                <li><NavLink className="link" activeClassName="nav_active" to="/txt"></NavLink></li>
                <li><NavLink className="link" activeClassName="nav_active" to="/fileback"></NavLink></li>
            </ul>
            <ul className='nav_ul2'>
            <Dropdown overlay={menu} className='dropDown'>
                    {/* // eslint-disable-next-line  */}
                    <li className="link"
                      style={{ fontSize: '21px',     
                      overflow: 'hidden',
                      textOverflow: 'ellipsis',
                      whiteSpace: 'nowrap',
                      maxWidth: '220px'}}
                    >
                        {mess}
                        <Icon className='navIcon'
                            spin
                            style={{ fontWeight:'bolder',fontSize: '25px'
                                     ,position:'relative',top:'2px'
                                  }}
                            type="down" />
                    </li>
                </Dropdown> 
            </ul>
        </nav>
    )
}

Navbar.defaultProps = {  //
    title: '',
};

Navbar.propTypes = {
    title: PropTypes.string.isRequired,
};
export default Navbar
