import React, {
    useState
    // , useContext
} from 'react'
import { Typography, Input, Button } from 'antd'
import { Link } from 'react-router-dom'
import '../cardReq/cardReq.scss'



const CardReq = () => {
    const [username, setName] = useState(''); // reactHook  useStatestate
    const [title, setTitle] = useState(''); // reactHook  useStatestate
    const [mail, setMail] = useState(''); // reactHook  useStatestate
    const [notice, setNotice] = useState(''); // reactHook  useStatestate
    const [number, setNumber] = useState(''); // reactHook  useStatestate
    const [called, setCalled] = useState(''); // reactHook  useStatestate


    const { Text } = Typography
    // 
    const handleTitleChange = (val) => {
        if (val.target.value.length > 30) { return }
        setTitle(val.target.value)
    }
    const handleNameChange = (val) => {
        if (val.target.value.length > 30) { return }
        setName(val.target.value)
    }
    const handleMailChange = (val) => {
        if (val.target.value.length > 5) { return }
        setMail(val.target.value)
    }
    const handleNoticeChange = (val) => {
        if (val.target.value.length > 140) { return }
        setNotice(val.target.value)
    }
    const handleNumberChange = (val) => {
        if (val.target.value.length > 25) { return }
        setNumber(val.target.value)
    }
    const handleCalledChange = (val) => {
        if (val.target.value.length > 25) { return }
        setCalled(val.target.value)
    }
    const handleSubmit = () => {
        console.log(called)
    }

    return (
        <div className='cardReq'>
            <div className='navText'>
                <Text strong className='navFont'></Text>
            </div>
            <div className='cardReq-wrap'>
                <div className='cardReq-wrap__title'></div>
                <div className='cardReq-wrap__box'>
                    <div className='cardReq-wrap__div'>
                        <Text  className="cardReq-wrap__text"></Text>
                        <Input
                            className="cardReq-wrap__input"
                            placeholder=""
                            value={title}
                            onChange={handleTitleChange}
                        />
                    </div>
                    <div className='cardReq-wrap__div'>
                        <Text  className="cardReq-wrap__text"></Text>
                        <Input
                            className="cardReq-wrap__input"
                            placeholder=""
                            value={username}
                            onChange={handleNameChange}
                        />
                    </div>
                </div>
                
                <div className='cardReq-wrap__box'>
                    <div className='cardReq-wrap__div'>
                        <Text  className="cardReq-wrap__text"></Text>
                        <Input
                            className="cardReq-wrap__input"
                            placeholder=""
                            value={called}
                            onChange={handleCalledChange}
                        />
                    </div>
                    <div className='cardReq-wrap__div'>
                        <Text  className="cardReq-wrap__text"></Text>
                        <Input
                            className="cardReq-wrap__input"
                            placeholder=""
                            value={mail}
                            onChange={handleMailChange}
                        />
                    </div>
                </div>

                <div className='vacationReq-wrap__box' style={{marginTop:'25px',width:'50%'}}>
                    <div className='vacationReq-wrap__div' >
                        <Text  className="vacationReq-wrap__text"></Text>
                        <Input
                            className="vacationReq-wrap__input"
                            placeholder=""
                            value={number}
                            type='number'
                            onChange={handleNumberChange}
                        />
                    </div>
                </div>


                <div className='cardReq-wrap__notice'>
                    <Text  className="cardReq-wrap__text"></Text>
                    <textarea
                        className="cardReq-wrap__textarea"
                        value={notice}
                        type='textarea'
                        onChange={handleNoticeChange}
                    />
                </div>

                <div className='cardReq-wrap__bottom'>
                    <Button type='danger' className='cardReq-wrap__bottom--btn' onClick={handleSubmit}></Button>
                    <Link to='/index/requestProcess' style={{ height: '100%' }}><Button className='cardReq-wrap__bottom--btn' ></Button></Link>
                </div>

            </div>



        </div>
    )
}

export default CardReq
