import React, { useState
    // , useContext
 } from 'react'
import { Typography, Input, Button, DatePicker } from 'antd'
import {Link} from 'react-router-dom'
import '../overTimeReq/overTimeReq.scss'



const OverTimeReq = () => {
    const [username, setName] = useState(''); // reactHook  useStatestate
    const [title, setTitle] = useState(''); // reactHook  useStatestate
    const [time, setTime] = useState(''); // reactHook  useStatestate
    const [notice, setNotice] = useState(''); // reactHook  useStatestate
    const [payback, setPayback] = useState(''); // reactHook  useStatestate
    const [work, setWork] = useState(''); // reactHook  useStatestate

    // const { MonthPicker, RangePicker } = DatePicker;

    const { Text } = Typography
    // 
    const handleTitleChange = (val) => {
        if(val.target.value.length>30){return}
        setTitle(val.target.value)
    }
    const handleNameChange = (val) => {
        if(val.target.value.length>30){return}
        setName(val.target.value)
    }
    const handleTimeChange = (val) => {
        if(val.target.value.length>5){return}
        setTime(val.target.value)
    }
    const handleNoticeChange = (val) => {
        if(val.target.value.length>140){return}
        setNotice(val.target.value)
    }
    const handlePaybackChange = (val) => {
        if(val.target.value.length>25){return}
        setPayback(val.target.value)
    }
    const handleWorkChange = (val) => {
        if(val.target.value.length>25){return}
        setWork(val.target.value)
    }
    const handleSubmit = () => {
    }

    return (
        <div className='overTimeReq'>
            <div className='navText'>
                <Text strong className='navFont'></Text>
            </div>
            <div className='overTimeReq-wrap'>
                <div className='overTimeReq-wrap__title'></div>
                <div className='overTimeReq-wrap__box'>
                    <div className='overTimeReq-wrap__div'>
                        <Text  className="overTimeReq-wrap__text"></Text>
                        <Input
                            className="overTimeReq-wrap__input"
                            placeholder=""
                            value={title}
                            onChange={handleTitleChange}
                        />
                    </div>
                    <div className='overTimeReq-wrap__div'>
                        <Text  className="overTimeReq-wrap__text"></Text>
                        <Input
                            className="overTimeReq-wrap__input"
                            placeholder=""
                            value={username}
                            onChange={handleNameChange}
                        />
                    </div>
                </div>
                
                <div className='overTimeReq-wrap__box'>
                    <div className='overTimeReq-wrap__div'>
                        <Text  className="overTimeReq-wrap__text"></Text>
                        <DatePicker className="overTimeReq-wrap__input"></DatePicker>
                        </div>
                    <div className='overTimeReq-wrap__div'>
                        <Text  className="overTimeReq-wrap__text"></Text>
                        <Input
                            className="overTimeReq-wrap__input"
                            placeholder=""
                            value={time}
                            type='number'
                            onChange={handleTimeChange}
                        />
                    </div>
                </div>

                <div className='overTimeReq-wrap__box' style={{marginTop:'25px'}}>
                    <div className='overTimeReq-wrap__div'>
                        <Text  className="overTimeReq-wrap__text"></Text>
                        <Input
                            className="overTimeReq-wrap__input"
                            placeholder=""
                            value={payback}
                            onChange={handlePaybackChange}
                        />
                    </div>
                    <div className='overTimeReq-wrap__div' >
                        <Text  className="overTimeReq-wrap__text"></Text>
                        <Input
                            className="overTimeReq-wrap__input"
                            placeholder=""
                            value={work}
                            onChange={handleWorkChange}
                        />
                    </div>
                </div>

                    
                    <div className='overTimeReq-wrap__notice'>
                        <Text  className="overTimeReq-wrap__text"></Text>
                        <textarea
                            className="overTimeReq-wrap__textarea"
                            value={notice}
                            type='textarea'
                            onChange={handleNoticeChange}
                        />
                    </div>

                <div className='overTimeReq-wrap__bottom'>
                    <Button type='danger' className='overTimeReq-wrap__bottom--btn' onClick={handleSubmit}></Button>
                    <Link to='/index/requestProcess' style={{height:'100%'}}><Button className='overTimeReq-wrap__bottom--btn' ></Button></Link>
                </div>

            </div>



        </div>
    )
}

export default OverTimeReq
